.grid-wrapper{
      display: grid;
      grid-gap: 2rem;
      grid-template-columns: repeat( auto-fill, minmax(350px, 2fr) );
      grid-template-rows: 1fr 1fr;
      margin: 0;
      padding: 0;
      box-sizing: border-box;
      
    }
    .grid-wrapper li{
      font-size: 24px;
      list-style-type: none;
      padding: 2rem 1rem;
      text-align: center;
      text-transform: capitalize;
      font-weight: 600;
      border: 1px solid #A8BECB;
      border-radius: 8px;
      box-shadow: 0px 4px 20px -8px rgba(0, 0, 0, 0.25);
      width: 100%;
      
    }
    .main-container{
      margin: 0 auto;
      max-width: 1150px;
      padding:2rem 1rem;
    }
