.custom-btn{
  color: rgb(0, 0, 0);
  background-color: #ffffff;
  border-color: black;
  border-width: 2px;
  font-weight: 400;
  width: 175px;
}

.custom-btn:hover {
  color: rgb(0, 0, 0);
  background-color: #ffffff;
  border-color: black;
}
.custom-btn:not(:disabled):not(.disabled).active, .custom-btn:not(:disabled):not(.disabled):active, .show>.custom-btn.dropdown-toggle {
  color: rgb(0, 0, 0);
  background-color: #ffffff;
  border-color: black;
}
.custom-btn.focus, .custom-btn:focus {
  color: rgb(0, 0, 0);
  background-color: #ffffff;
  border-color: black;
  box-shadow: 0 0 0 0 #fff;
}

.custom-btn:not(:disabled):not(.disabled).active:focus, .custom-btn:not(:disabled):not(.disabled):active:focus, .show>.custom-btn.dropdown-toggle:focus {
  box-shadow: 0 0 0 0 #fff;
}
